<template>
	<view>
		<cu-custom bgColor="bg-blue-one" :isBack="true" :isR="false">
			<block slot="content">用户认证</block>
		</cu-custom>
		<form @submit="formSubmit">
			<view class="grid col-3 text-center margin-top-xl bg-white padding-bottom-xl">
				<view class="cu-item" @tap="addIdCardImage()">
					<view class="cuIcon-cu-image padding">
						<image class="icon_tb" :src="IdImageUrl"></image>
					</view>
					<text>身份证人像照</text>
				</view>
				<view class="cu-item" @tap="addDrivingImage()">
					<view class="cuIcon-cu-image padding">
						<image class="icon_tb" :src="DrivingImageUrl"></image>
					</view>
					<text>行驶证正业</text>
				</view>
				<view class="cu-item" @tap="addDriverImage()">
					<view class="cuIcon-cu-image padding">
						<image class="icon_tb" :src="DriverImageUrl"></image>
					</view>
					<text>驾驶证正业</text>
				</view>
			</view>
			<view class="cu-form-group margin-top">
				<view class="title">姓名</view>
				<input style="padding-left: 60px;" placeholder="上传照片自动获取" name="input" v-model="username"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">身份证</view>
				<input style="padding-left: 45px;" placeholder="上传照片自动获取" name="input" v-model="Idcard"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">车牌号</view>
				<input style="padding-left: 45px;" placeholder="上传照片自动获取" name="input" v-model="PlateNum"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">车辆识别代号</view>
				<input placeholder="上传照片自动获取" name="input" v-model="vehicleNum"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">手机号</view>
				<input style="padding-left: 45px;" name="input" v-model="TellNum"></input>
			</view>
			<view class="padding flex flex-direction" style="padding-top: 80upx;">
				<button class="cu-btn bg-blue-one lg" form-type="submit">提交</button>
			</view>
		</form>
	</view>
</template>

<script>
var that

export default {
  data () {
    return {
      IdImageUrl: '../../static/drawable-xhdpi/wode_icon1_pic.png',
      DriverImageUrl: '../../static/drawable-xhdpi/wode_icon1_pic.png',
      DrivingImageUrl: '../../static/drawable-xhdpi/wode_icon1_pic.png',
      username: '',
      Idcard: '',
      PlateNum: '',
      vehicleNum: '',
      TellNum: '',
      IdcardUrl: 'https://aip.baidubce.com/rest/2.0/ocr/v1/idcard?access_token=',
      imageBase64Str: "",
      access_token: '',
      APIKey: 'apQvNW2D0i62ks56PGKKvUEl',
      SecretKey: '4ccxePr1s8yZ7Y1wZdKXR3d6hMm7qdhA'
    }
  },
  onLoad () {
    that = this
    that.getAccess_token()
  },
  methods: {
    getAccess_token () {
      var accessToken = that.cache("accessToken", null, null);
      if (!accessToken) {
        uni.request({
          method: 'POST',
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          url: "/api/oauth/2.0/token",
          data: {
            grant_type: "client_credentials",
            client_id: this.APIKey,
            client_secret: this.SecretKey
          },
          success: function (res) {
            console.log(res);
            uni.setStorageSync("Baidu", res.data);
            that.access_token = res.data.access_token;
            that.cache("accessToken", res.data.access_token, null);
          }
        })
      } else {
        that.access_token = accessToken;
      }
    },
    // 默认有效期28天
    cache: function (key, value, seconds) {
      var timestamp = Date.parse(new Date()) / 1000
      if (key && value === null) {
        //删除缓存
        //获取缓存
        var val = uni.getStorageSync(key);
        var tmp = val.split("|")
        if (!tmp[1] || timestamp >= tmp[1]) {
          console.log("key已失效")
          uni.removeStorageSync(key)
          return ""
        } else {
          console.log("key未失效")
          return tmp[0]
        }
      } else if (key && value) {
        //设置缓存
        if (!seconds) {
          var expire = timestamp + (3600 * 24 * 28)
        } else {
          var expire = timestamp + seconds
        }
        value = value + "|" + expire
        uni.setStorageSync(key, value);
      } else {
        console.log("key不能空")
      }
    },
    getIDCard () {
      var flag = true
      if (flag) {
        console.log("==")
        //百度名片识别接口处理开始
        uni.showLoading({
          mask: true,
          title: "识别中，请等待"
        });
        uni.request({
          url: that.IdcardUrl + that.access_token,
          method: 'POST',
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          data: {
            image: that.IdImageUrl,
            id_card_side: "front"
          },
          success: function (res) {
            console.log(res);
            var IdCard = res.data.words_result.公民身份号码.words;
            var userName = res.data.words_result.姓名.words;
            if (IdCard == that.IdNumber && userName == that.name) {
              uni.showToast({
                icon: 'success',
                title: '身份验证成功'
              });
            } else {
              uni.showToast({
                icon: 'none',
                title: '身份信息有误请审核信息'
              });
            }
          },
          complete () {
            uni.hideLoading();
          }
        })
      }
    },
    formSubmit: function (e) {
      this.getIDCard()
    },
    // 身份证
    addIdCardImage () {
      var that = this
      uni.chooseImage({
        count: 1,
        sizeType: ['original', 'compressed'],
        sourceType: ['album', 'camera'],
        success: function (res) {
          const tempFilePaths = res.tempFilePaths;
          uni.uploadFile({
            url: '',
            fileType: "image",
            filePath: tempFilePaths[0],
            name: 'imgFile',
            success: (uploadFileRes) => {
              that.IdImageUrl = tempFilePaths[0]
              that.getIDCard()
              // let imgData = JSON.parse(uploadFileRes.data)
              // 上传图片后处理的逻辑
              // this.imagesUrl = imgData.data.imgUrl
            }
          });
        }
      });
    },
    // 驾驶证
    addDriverImage () {
      var that = this
      uni.chooseImage({
        count: 1,
        sizeType: ['original', 'compressed'],
        sourceType: ['album', 'camera'],
        success: function (res) {
          const tempFilePaths = res.tempFilePaths;
          that.DriverImageUrl = tempFilePaths[0]
          uni.uploadFile({
            url: '',
            fileType: "image",
            filePath: tempFilePaths[0],
            name: 'imgFile',
            success: (uploadFileRes) => {
              // let imgData = JSON.parse(uploadFileRes.data)
              // 上传图片后处理的逻辑
              // this.imagesUrl = imgData.data.imgUrl
            }
          });
        }
      });
    },
    // 行驶证
    addDrivingImage () {
      var that = this
      uni.chooseImage({
        count: 1,
        sizeType: ['original', 'compressed'],
        sourceType: ['album', 'camera'],
        success: function (res) {
          const tempFilePaths = res.tempFilePaths;
          that.DrivingImageUrl = tempFilePaths[0]
          uni.uploadFile({
            url: '',
            fileType: "image",
            filePath: tempFilePaths[0],
            name: 'imgFile',
            success: (uploadFileRes) => {
              // let imgData = JSON.parse(uploadFileRes.data)
              // 上传图片后处理的逻辑
              // this.imagesUrl = imgData.data.imgUrl
            }
          });
        }
      });
    },
    //校验输入的数据
    verifyData () {
      var flag = true;
      if (!this.username) {
        flag = false;
        uni.showToast({
          icon: 'none',
          title: '姓名不能为空'
        });
        return false;
      }
      if (!this.Idcard) {
        flag = false;
        uni.showToast({
          icon: 'none',
          title: '身份证号不能为空'
        });
        return false;
      }
      var re15 = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$/;
      var re18 = /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{4}$/;
      var res = (re15.test(this.Idcard) || re18.test(this.Idcard));
      if (res == false) {
        flag = false;
        uni.showToast({
          icon: 'none',
          title: '身份证号填写有误'
        });
        return false;
      }
      if (!this.imageBase64Str) {
        flag = false;
        uni.showToast({
          icon: 'none',
          title: '请上传身份证含照片一面'
        });
        return false;
      }
      return flag;
    },
  },
}
</script>

<style>
.icon_tb {
  height: 60px;
}

.title {
  color: #000;
  font-weight: 700;
}

.cu-item > text {
  color: #000;
  font-weight: 700;
}
button {
  border-radius: 10px;
}
</style>
